#card-carousel {
    background: $carousel-bg;
    padding: 46px 0;
    height: auto;
    overflow: hidden;

    h2 {
        text-align: center;
        color: $purple;
        margin-bottom: 40px;
    }

    .cards {
        .card {
            .card-content {
                height: auto;
                padding-bottom: 0;

                span {
                    margin: 14px 0;
                }

                .heart {
                    display: none;
                }
            }
        }
    }
}

.carousel {
    padding: 0;
}

//Slick styles

.slick-slide {
    outline: none;
}

.slick-dots {
    >li {
        display: inline-block;
        margin: 0 2px;

        &:only-child {
            display: none;
        }

        &.slick-active {
            button {
                background: $purple;
            }
        }

        button {
            background: $gray-circle;
            width: 15px;
            height: 15px;
            border-radius: 10px;

            &:before {
                content: "";
            }
        }
    }
}

//Image carousel inside modal
.image-carousel {
    min-height: 450px;
    @media only screen and (max-width: 64em) {
        min-height: auto;
    }
    .slick-dots {
        text-align:left;
        margin-left:5px;
    }
}