.filter-container{
    padding:20px 0;
    border-top:1px $gray-border solid;
    border-bottom:1px $gray-border solid;
    margin-bottom:20px;
    .filter{
        background: $gray-bg;
        border:1px $gray-border solid;
        color:$gray-text;
        padding:10px 15px;
        font-size:1rem;
        border-radius:5px;
        font-weight:300;
        display:inline-block;
        margin-right:10px;
        margin-bottom:10px;
        i{
            color:$purple;
            right:15px;
            cursor:pointer;
            margin-left:40px;
            font-size:12px;
        }
    }
}