#not-elegible {
    padding: 50px 20px;
    background: $purple-med;

    p {
        color: white;
        // font-weight: bold;
        margin-bottom: 0;
        @media only screen and (max-width: 60em) {
            text-align:center;
        }
    }

    a.button {
        float: right;
        @media only screen and (max-width: 60em) {
            float:none;
            display:table;
            margin:20px auto 0 auto;
            padding-top:12px!important;
        }
    }
}

#not-elegible-modal {
    padding: 50px 20px;
    p {
        color: $purple;
        font-weight: bold;
        font-size:1.5rem;
        line-height: 1.75rem;
        @media only screen and (max-width: 32em) {
            margin-bottom:20px;
        }
    }
}