#browse-offers {
    padding: 40px 12px;
    background: $bg-gray-dark;
    margin: 10px 10px 0;

    h2 {
        color: $purple;
        font-weight: 700;

        @media only screen and (max-width: 64em) {
            margin-bottom: 50px;
        }
    }

    strong {
        color: $purple;
        font-family: "helvetica_neue";
    }

    select {
        border: 1px $gray-border solid;
        color: $input-color;
    }

    .loader {
        display: none;
        position: relative;
        border: 5px solid white;
        border-radius: 50%;
        border-top: 5px solid $purple;
        width: 50px;
        height: 50px;
        -webkit-animation: spin 2s linear infinite;
        /* Safari */
        animation: spin 2s linear infinite;
        z-index: 100;
        margin: 80px auto;

        &.show {
            display: block;
        }
    }

    .search {
        position: relative;

        &.loading {
            .loader {
                display: block !important;
            }
        }

        .loader {
            display: none;
            position: absolute;
            left: 87%;
            top: 10px;
            border: 3px solid #f3f3f3;
            border-radius: 50%;
            border-top: 3px solid $purple;
            width: 20px;
            height: 20px;
            -webkit-animation: spin 2s linear infinite;
            /* Safari */
            animation: spin 2s linear infinite;
            margin: 0;
        }

        /* Safari */
        @-webkit-keyframes spin {
            0% {
                -webkit-transform: rotate(0deg);
            }

            100% {
                -webkit-transform: rotate(360deg);
            }
        }

        @keyframes spin {
            0% {
                transform: rotate(0deg);
            }

            100% {
                transform: rotate(360deg);
            }
        }
    }

    input[type="radio"] {
        cursor: pointer;
        margin: 0 0 0.5rem;
    }

    input[type="checkbox"] {
        cursor: pointer;
        border-radius: 2px !important;
        padding: 2px;

        &:checked {
            background-image: url(../images/check_checked2.png);
            border: 1px #8b75ae solid !important;
            background-size: cover;
            background-color: #4f2d84;
        }
    }

    .primary-filters-container {
        display: flex;
        border-top: 1px $gray-border solid;
        padding-top: 20px;
    }

    /* The switch - the box around the slider */
    .switch {
        position: relative;
        display: inline-block;
        width: 38px;
        height: 20px;
        margin-bottom: 10px;

        ~span {
            color: $gray-text;
            padding-left: 7px;
            font-size: 0.875rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;
        }

        /* Hide default HTML checkbox */
        input {
            opacity: 0;
            width: 0;
            height: 0;
        }
    }

    /* The slider */
    .slider-toggle {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: $gray-text;
        -webkit-transition: 0.4s;
        transition: 0.4s;

        &:before {
            position: absolute;
            content: "";
            height: 17px;
            width: 16px;
            background-color: white;
            transition: 0.4s;
            right: 20px;
            top: 2px;
        }

        /* Rounded sliders */
        &.round {
            border-radius: 34px;

            &:before {
                border-radius: 50%;
            }
        }
    }

    input:checked+.slider-toggle {
        background-color: $purple;
    }

    input:focus+.slider-toggle {
        box-shadow: 0 0 1px $purple;
    }

    input:checked+.slider-toggle:before {
        -webkit-transform: translateX(18px);
        -ms-transform: translateX(18px);
        transform: translateX(18px);
    }

    .custom-radio {
        display: block;
        position: relative;

        input[type="radio"] {
            display: none;
        }

        label {
            color: $gray-text;
            margin-left: 0;
            cursor: pointer;
        }

        i {
            margin-left: 10px;
        }
    }

    .custom-radio label:before {
        content: " ";
        display: inline-block;
        position: relative;
        top: 5px;
        margin: 0 8px 0 0;
        width: 20px;
        height: 20px;
        border-radius: 11px;
        border: 1px solid $purple;
        background-color: transparent;
    }

    .custom-radio input[type="radio"]:checked+label:after {
        border-radius: 11px;
        width: 12px;
        height: 12px;
        position: absolute;
        top: 9px;
        left: 4px;
        content: " ";
        display: block;
        background: $purple;
    }

    .checkbox {
        position: relative;

        label {
            position: absolute;
            top: 2px;
            left: 20px;
            color: $gray-text;
            cursor: pointer;
            line-height: 12px;
        }

        i {
            color: #919d9d;
            margin-left: 10px;
        }
    }

    .search-bar {
        position: sticky;
        top: 80px;
        z-index: 9;
        margin-bottom: 25px;
        transition: 300ms ease background-color, 500ms padding;

        @media only screen and (max-width: 60em) {
            top: 48px;
        }

        &.is-pinned {
            background-color: $gray-cards;
            padding: 15px 15px;
            border-bottom: 1px solid #ccc;
            margin-left: 25px;
            margin-right: 5px;
            box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.1);
            transition:
                300ms ease background-color,
                500ms ease color,
                500ms padding;

            @media only screen and (max-width: 60em) {
                margin-left: 5px;

                &.with-translate-bar {
                    padding-top: 23px;
                    top: 80px;
                }
            }

            .search {
                &#store-search {
                    margin: 0;

                    input {
                        margin-bottom: 0;
                    }
                }
            }
        }

        .top-marker,
        .bottom-marker {
            height: 0px;
            left: 0px;
            position: absolute;
            width: 100%;
        }

        .top-marker {
            height: 2px;
            top: -1px;
        }

        .bottom-marker {
            bottom: -1px;
        }

        .search {
            &#store-search {
                position: relative;
                margin-left: 25px;
                margin-right: 5px;
                font-size: 1.5rem;

                @media only screen and (max-width: 60em) {
                    margin-left: 5px;
                    margin-right: 5px;
                }

                input {
                    width: 100%;
                    position: relative;
                    border-radius: 4px;
                    border-style: solid;
                    border-width: 1px;
                    -webkit-box-shadow: none;
                    box-shadow: none;
                    text-align: left;
                    padding-top: 5px;
                    padding-bottom: 5px;
                    border-color: #ccc;
                    padding: 5px 25px;
                    padding-left: 40px;
                    line-height: 30px;
                    height: 41px;
                    font-size: 15px;
                    margin-bottom: 0;
                }

                i {
                    position: absolute;
                    left: 8px;
                    top: 0px;
                    font-size: 18px;
                    color: $purple;
                    width: 30px;
                    height: 40px;
                    text-align: center;
                    line-height: 40px;

                    &.fa-times {
                        cursor: pointer;
                        display: none;
                    }
                }

                .loader {
                    left: unset;
                    right: 30px;
                }

                i.fa-times {
                    left: calc(100% - 30px);
                    top: 0;
                    display: none;
                }
            }
        }
    }

    .button.large {
        display: table;
        margin: 0 auto;
        padding: 15px 30px;
        font-weight: bold;
        font-size: 1rem;
        font-weight: 600;
    }

    strong {
        &.title {
            font-weight: 600;
            font-size: 1.75rem;
        }
    }

    .hide-mobile {
        padding-bottom: 15px;
    }

    .contact-us {
        background: $purple;
        padding: 0.5rem;
        margin: 50px 0;

        @media only screen and (max-width: 60em) {
            display: none;
        }

        p {
            color: white;
            text-transform: uppercase;
            font-size: 0.75rem;
            letter-spacing: 0.15rem;
            text-align: center;
            margin-bottom: 5px;
        }

        div {
            display: flex;
        }

        a {
            text-align: center;
            flex: auto;

            i {
                width: 20px;
                height: 20px;
                border-radius: 20px;
                background: $yellow;
                color: $purple;
                text-align: center;
                line-height: 20px;
                margin-right: 5px;
            }
        }
    }

    //Mobile Menu
    .mobile-menu {
        @media only screen and (max-width: 60em) {
            position: fixed;
            top: 48px;
            left: -1000px;
            opacity: 0;
            z-index: 110;
            width: 100%;
            height: 100%;
            background: $bg-gray-dark;
            padding: 0;
            max-height: 100vh;
            overflow: hidden;

            .hide-mobile {
                display: none;
            }

            &.with-translate-bar {
                top: 88px;
            }
        }

        &.show {
            opacity: 1;
            left: 0;

            .top-filter {
                margin-top: 0;
                height: 72%;
                overflow-y: auto;
                padding-left: 30px;
                padding-right: 25px;
            }
        }
    }

    .continue {
        position: fixed;
        bottom: 0;
        background: white;
        width: 87%;
        transition: 0.5s;
        height: 10%;
        display: flex;
        align-items: center;
        margin-left: 25px;
        padding-right: 25px;
        padding-left: 25px;
        z-index: 100;

        @media only screen and (min-width: 60em) {
            display: none !important;
        }

        a {
            width: 100%;
            text-align: center;
            text-transform: capitalize;
            padding: 10px;
            height: auto;
            display: inline-block;
            line-height: 1;
        }
    }

    .display-menu {
        position: relative;

        @media only screen and (min-width: 60em) {
            display: none;
        }

        a {
            border: 1px $gray-border solid;
            background: white;
            color: $input-color;
            padding: 7px 11px 6px;
            border-radius: 5px !important;
            font-style: italic;
            width: 100%;
            display: inline-block;
            margin-top: 30px;

            &:after {
                content: "";
                position: absolute;
                right: 16px;
                top: 67%;
                transition: all 0.2s ease-in-out;
                display: block;
                width: 6px;
                height: 6px;
                border-top: solid 2px black;
                border-right: solid 2px black;
                -webkit-transform: rotate(135deg);
                transform: rotate(44deg);
            }
        }
    }

    .suggestion-box-mkp {
        max-height: 200px;
        width: 100%;
        overflow-y: auto;
        position: absolute;
        right: 0;
        z-index: 9;
        border: none;
        background: white;
        top: 42px;
        //@extend .box-shadow-complete;

        a {
            width: 100%;
            display: block;
            margin: 0;
            padding: 10px 15px !important;
            border: none;
            text-align: left;
            cursor: pointer;
            color: $purple;
            font-size: 14px;

            &:hover {
                background: $gray_bg;
            }
        }
    }
}

// Cards Styles

.cards {
    position: relative;

    @media only screen and (min-width: 60.063em) {
        display: inherit !important;
        padding-left: 20px;
    }

    &.loading {
        .loader {
            display: block !important;
        }

        .card {
            opacity: 0.1;
        }
    }

    .card {
        padding: 0 10px 20px 10px;
        outline: none;

        .card-content {
            // height: 234px;
            border: 1px $gray-border solid;
            border-radius: 3px;
            cursor: pointer;
            background: white;
            padding-bottom: 10px;
            position: relative;

            .heart {
                position: absolute;
                top: 4px;
                right: 7px;

                &.active {
                    i {
                        color: $purple;
                    }
                }

                i {
                    color: $heart-bg;
                    transition: 0.5s;
                }
            }

            .top {
                background: $purple-med;
                padding: 10px;
                display: none;
                height: 64px;
                align-items: center;

                &.show {
                    display: flex;
                }

                p {
                    text-align: center;
                    color: white;
                    font-size: 1.4rem;
                    margin: 0;
                    line-height: 1.25rem;
                    flex: auto;
                }
            }

            img {
                margin: 0 auto;
                max-width: 100px;
                display: block;
                padding: 15px 0;
            }

            .gray-content {
                background: $gray-cards;
                padding: 20px 0;
                height: 145px;

                p {
                    color: $purple;
                    font-weight: 900;
                    font-size: 1rem;
                    text-align: center;
                    margin-bottom: 0;
                    margin-top: 20px;
                    font-family: "open_sansextrabold";
                }

                .button {
                    text-transform: capitalize;
                    display: table;
                    margin: 0 auto;
                    padding: 10px;
                    font-weight: bold;

                    @media only screen and (max-width: 60em) {
                        font-size: 0.8rem;
                    }

                    &.white {
                        background: white;
                        color: $purple-light  !important;
                    }
                }
            }

            span {
                display: block;
                text-align: center;
                font-size: 0.75rem;
                margin-top: 14px;
            }

            small {
                text-align: center;
                display: block;
                color: $purple;
            }
        }
    }

    .card:last-child {
        float: left;
    }
}

.sc-experience-editor {
    .card-banner {
        display: flex !important;
        width: 95%;
        margin-left: 25px;
    }
}

.card-banner {
    background-color: $purple;
    color: white;
    border-radius: 5px;
    padding: 8px 18px;
    font-size: 15px;
    font-weight: 300;
    align-items: center;
    flex-direction: column;
    display: none;
    margin-left: 12px;
    width: 97%;
    margin-bottom: 20px;

    @media screen and (min-width: 60em) {
        flex-direction: row;

        span {
            padding-right: 10px;
        }
    }

    a.button {
        font-size: 14px;
        min-width: 160px;
        margin-top: 5px;

        @media screen and (min-width: 60em) {
            margin-left: auto;
            margin-top: 0;
        }
    }

    &.visible-sm-flex {
        span {
            text-align: center;
        }
    }
}

.store-no-results {
    padding-top: 20px;
    font-size: 16pt;
    text-align: center;
}

.tier-padding {
    @media screen and (max-width: 32em) {
        padding-left: 22px;
        padding-right: 22px;
    }

    @media screen and (max-width: 20.4em) {
        padding-left: 0;
        padding-right: 0;
    }
}

.overflowHidden {
    overflow-y: hidden;
    margin: 0 !important;
    height: auto;
}

.filter-label {
    font-size: 25px;
    font-weight: bold;
    padding: 10px 20px 5px 25px;
    width: 100%;
    margin-bottom: 0;
    @media only screen and (min-width: 60em) {
        display: none !important;
    }
}