#faq {
    background: $bg-gray-dark;
    padding: 94px 65px;
    margin: 0 10px 10px;
    @media only screen and (max-width: 40em) { 
        padding: 15px;
    }
    @media only screen and (min-width: 60.063em) {
        display: block!important;
    }

    h2 {
        color: $purple;
        font-weight: 700;
        text-align: center;
        margin-bottom: 20px;
    }
    .columns{
        > p{
            text-align:center;
        }
    }
    .accordion-container{
        .accordion-title{
            border-top:2px #C8D0D0 solid;
            font-size: 1.25rem;
            padding:24px 20px 24px 0;
            color:black;
            line-height:1.75rem;
            font-family:'open_sanssemibold';
            @media only screen and (max-width: 40em) { 
                padding:24px 24px 24px 0;
            }
            &:after{
                width: 13px;
                height: 13px;
                border-top: solid 3.5px $purple;
                border-right: solid 3.5px $purple;
            }
        }
        .accordion-content{
            padding: 10px 0;
            p{
                color:$gray-text;
            }
        }
    }
}