#hero,
#card-carousel,
#browse-offers,
#faq,
#steps,
#options,
.reveal.marketplace {
  h1 {
    font-size: 30px;
    font-weight: bold;
  }

  h2 {
    font-size: 26px;
    font-weight: bold;
  }

  h3 {
    font-size: 22px;
    font-weight: bold;
  }

  h4 {
    font-size: 18px;
    font-weight: bold;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: 'open_sansextrabold';
  }
}

.title-get-started {
  font-family: 'helvetica_neue' !important;

  @media only screen and (max-width: 64em) {
    font-size: 1.35rem !important;
  }
}