.reveal {
    &.marketplace {
        background: $modal-bg;
        padding: 0;
        outline: none;
        overflow: hidden;
        // min-height: 412px;

        @media screen and (min-width: 64.063em) {
            width: 800px !important;
            max-width: 972px;
        }

        @media only screen and (min-width: 40.063em) and (max-width: 64em) {
            width: 650px !important;
            left: 8% !important;
        }

        @media only screen and (min-width: 32.63em) and (max-width: 40em) {
            left: 2% !important;
        }

        // @media screen and (max-width: 32em) {
        //     top: 460px !important;
        // }

        @media screen and (max-width: 32em) {
            // top: 50% !important;
            // transform: translateY(-50%);
            position: fixed;
            top: 40% !important;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .close-button {
            top: 10px;
            right: 10px;
            span {
                position: absolute;
            }
        }

        h2 {
            text-align: center;
            color: #512E85;
            margin-top: 20px;
            margin-bottom: 15px;
            @media screen and (max-width: 32em) {
                margin-top: 35px;
            }
        }

        p {
            text-align: center;

            img {
                filter: invert(1);
                margin-left: 5px;
            }
        }

        input {
            width: 40%;
            margin: 0 auto;
            text-align: center;
            pointer-events:none;
            @media screen and (max-width: 32em) {
                width: 95%;
            }
        }

        p {
            font-size: .85rem;
            line-height: 16px;
            margin: 15px auto;
            width: 73%;
            font-weight: 600;

            @media screen and (max-width: 32em) {
                width: 100%;
                padding: 0 15px;
            }

            &.bold {
                font-weight: 700;
            }

            a {
                color: $purple;
                font-size: .95rem;
            }
        }

        a.button {
            display: block;
            width: 23%;
            margin: 0 auto 20px;
            height: 40px;
            padding: 12px;
            @media screen and (max-width: 32em) {
                width: 95%;
            }
        }
        
        .screen-one {
            .carousel-container {
                background: white;
                // margin: 30px 0 15px;

                // padding:0 30px;
                @media only screen and (min-width: 64.1em) {
                    //padding-left: 40px;
                }

                h2 {
                    font-family: 'helvetica_neue';
                    color: black;
                    font-size: 1.4rem;
                    margin-top: 15px !important;
                }

                .carousel {
                    p {
                        margin: 0;
                        width: 100%;
                        text-align: left;
                        font-size: 14px !important;
                        color: $gray-text;
                        font-weight: 300;
                    }

                    // .slick-slide {
                    //     @media only screen and (min-width: 64.1em) {

                    //         &:nth-child(3),
                    //         &:nth-child(4) {
                    //             width: 22% !important;
                    //             float: left;
                    //             padding-left: 10px;
                    //             padding-right: 10px;
                    //         }
                    //     }
                    // }
                }
            }

            .error {
                margin: 10px;
                color: #721c24;
                background-color: #f8d7da;

                p {
                    color: #721c24;
                    padding: 10px;
                }
            }
        }

        .screen-two {
            display: none;
            position: relative;

            .skip {
                right: 10px;
                position: absolute;
                z-index: 100;
                display: block;
                bottom: 6px;
                color: $purple;
                font-size: .9rem;
            }

            .close-button {
                top: -10px;
                z-index: 200;
                background: $modal-bg;
                @media only screen and (max-width: 32em) { 
                    top: -25px;
                }

                span {
                    position: absolute;
                    top: 12px;
                    left: 12px;
                }
            }
        }
    }

    // Help Modal
    &#help-modal {
        @media only screen and (max-width: 64em) {
            top: 300px !important;
        }

        h2 {
            font-size: 1.25rem;
            text-align: left;
            padding: 0 1rem;
        }
    }
}