#hero {
    min-height: 254px;

    .columns {
        &.shadow {
            margin-top: 82px;
            height: 254px;
            box-shadow: inset 0 0 150px 250px rgba(81, 46, 133, 0.4);

            @media only screen and (max-width: 64em) {
                margin-top: 48px;
            }
        }
    }

    h1 {
        color: white;
        text-align: center;
        font-family: inherit;
        font-size: 2.9rem;
        letter-spacing: -1px;
        margin-top: 70px;

        @media only screen and (max-width: 64em) {
            font-size: 2rem;
        }

    }

    p {
        color: white;
        font-size: 1.1rem;
        font-weight: 300;
        text-align: center;
    }
}

#steps {
    .get-started {
        overflow: hidden;

        @media only screen and (max-width: 64em) {
            padding-bottom: 10px;
        }

        >div {
            background: white;
            padding: 2rem 1rem 0rem;

            @media only screen and (min-width: 64.1em) {
                margin-left: 82px;
                margin-bottom: 0;
            }

            @media only screen and (max-width: 64em) {
                padding: .5rem;
            }

            p {
                color: $gray-text;
                text-align: left;
                line-height: 1.25rem;
                font-size: .95rem;

                @media only screen and (max-width: 64em) {
                    width: 80%;
                }
            }

            .slick-slide {
                @media only screen and (min-width: 64.1em) {

                    // &:nth-child(1){
                    //     margin-left: 40px;
                    //     margin-right: 20px;
                    // }
                    // &:nth-child(2){
                    //       margin-right: 20px;
                    // }
                    &:nth-child(3),
                    &:nth-child(4) {
                        width: 21% !important;
                        float: left;
                        padding-left: 10px;
                        padding-right: 10px;
                    }
                }

                @media screen and (max-width: 32em) {
                    padding: 0 3rem;
                }
            }

        }
    }
}

.step {
    float: left;
    width: 35px;
    height: 35px;
    border-radius: 20px;
    background: $purple;
    color: $yellow;
    text-align: center;
    line-height: 2.2rem;
    margin-right: 10px;
    margin-bottom: 80px;

    @media only screen and (max-width: 40em) {
        margin-bottom: 0;
    }
}

#options {
    .options {
        padding: 30px 10px;

        a {
            color: $purple;
            font-size: .9rem;
            font-weight: 900;
            margin-left: 30px;
            font-family: 'helvetica_neue';

            &:first-child {
                margin-left: 10px;
            }

            @media only screen and (max-width: 64em) {
                margin-left: 0 !important;
            }
        }
    }
}